.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 1rem;
    background-color: #f9f9f9;
  }
  
  .login-box {
    width: 100%;
    max-width: 400px;
    background: white;
    padding: 2rem;
    border-radius: 12px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .login-image {
    border: 2px solid #ccc;
    height: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #888;
    margin-bottom: 1.5rem;
    font-size: 0.9rem;
  }
  
  .login-title {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }
  
  .login-form .input-group {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 6px;
    margin-bottom: 1rem;
    padding: 0.5rem 0.75rem;
  }
  
  .input-group .icon {
    margin-right: 0.5rem;
    font-size: 1.2rem;
    color: #666;
  }
  
  .input-group input {
    border: none;
    outline: none;
    flex: 1;
    font-size: 1rem;
  }
  
  .login-button {
    width: 100%;
    padding: 0.75rem;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1rem;
    transition: background 0.3s;
  }
  
  .login-button:hover {
    background-color: #555;
  }
  
  .register-text {
    margin-top: 1rem;
    font-size: 0.9rem;
  }
  
  .register-link {
    color: #007bff;
    text-decoration: none;
  }
  
  .register-link:hover {
    text-decoration: underline;
  }
  