.inicio-container {
    font-family: Arial, sans-serif;
    padding: 1rem;
    text-align: center;
  }
  
  .nav-bar {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
  }
  
  .nav-button {
    padding: 0.5rem 1rem;
    border: 2px solid #999;
    border-radius: 5px;
    background-color: white;
    cursor: pointer;
  }
  
  .nav-button.active {
    background-color: #a9cfff;
    font-weight: bold;
  }
  
  .title {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .image-container {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
  }
  
  .radio-image {
    max-width: 100%;
    width: 400px;
    border: 2px solid #000;
  }
  
  .action-buttons {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-top: 1rem;
  }
  
  .main-button {
    padding: 0.6rem 1.2rem;
    border: 2px solid #444;
    border-radius: 8px;
    background-color: white;
    font-size: 1rem;
    cursor: pointer;
  }
  