.nav-bar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #1f1f1f;
    padding: 1rem;
    border-bottom: 2px solid #333;
  }
  
  .nav-button {
    background-color: transparent;
    border: none;
    color: white;
    font-size: 1rem;
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .nav-button:hover {
    background-color: #333;
    border-radius: 5px;
  }
  
  .nav-button.active {
    background-color: #555;
    border-radius: 5px;
    font-weight: bold;
  }
  