.personas-page {
    font-family: Arial, sans-serif;
    padding: 1rem;
  }
  
  .personas-header h1 {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .personas-nav {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .nav-button {
    padding: 6px 12px;
    border: 1px solid gray;
    background-color: white;
    cursor: pointer;
  }
  
  .nav-button.active, .nav-button:hover {
    background-color: #cce0ff;
  }
  
  .personas-main h2 {
    text-align: center;
  }
  
  .search-input {
    width: 100%;
    padding: 8px;
    margin: 10px 0;
    font-size: 14px;
  }
  
  .personas-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .personas-table th, .personas-table td {
    border: 1px solid #aaa;
    padding: 8px;
    text-align: left;
  }
  
  .personas-table th {
    background-color: #f2f2f2;
  }
  
  .bottom-buttons {
    display: flex;
    justify-content: space-between;
  }
  
  .primary-button, .secondary-button {
    padding: 8px 12px;
    font-size: 14px;
    cursor: pointer;
    border: 1px solid #444;
    background-color: white;
  }
  
  .primary-button:hover, .secondary-button:hover {
    background-color: #eee;
  }
  