.role-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #f9f9f9;
    padding: 1rem;
  }
  
  .role-box {
    width: 100%;
    max-width: 400px;
    background: white;
    border-radius: 12px;
    padding: 2rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .role-image {
    border: 2px solid #ccc;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #888;
    margin-bottom: 1.5rem;
  }
  
  .role-title {
    font-size: 1.8rem;
    margin-bottom: 1.5rem;
  }
  
  .role-select-group {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1.5rem;
  }
  
  .role-icon {
    font-size: 1.5rem;
    margin-right: 0.5rem;
  }
  
  .role-select {
    font-size: 1rem;
    padding: 0.5rem;
    width: 200px;
  }
  
  .role-button {
    width: 100%;
    padding: 0.75rem;
    background-color: #333;
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
    margin-bottom: 1rem;
  }
  
  .role-button:hover {
    background-color: #555;
  }
  
  .back-button {
    background: transparent;
    border: none;
    color: #007bff;
    font-size: 0.9rem;
    cursor: pointer;
    text-decoration: underline;
  }
  